"use strict";

/************ nodeList.forEach polyfill *******************/
(function () {
  if (window.NodeList && !NodeList.prototype.forEach) {
    NodeList.prototype.forEach = Array.prototype.forEach;
  }
})();
/************ matches polyfill *******************/


(function () {
  if (!Element.prototype.matches) {
    Element.prototype.matches = Element.prototype.matchesSelector || Element.prototype.webkitMatchesSelector || Element.prototype.mozMatchesSelector || Element.prototype.msMatchesSelector;
  }
})();
/************ closest polyfill *******************/


(function () {
  if (!Element.prototype.closest) {
    Element.prototype.closest = function (css) {
      var node = this;

      while (node) {
        if (node.matches(css)) return node;else node = node.parentElement;
      }

      return null;
    };
  }
})();

window.addEventListener('DOMContentLoaded', function () {
  /***************************MODAL HANDLER START*******************************/
  var showPopupBtns = document.querySelectorAll('[data-popup-toggle]');
  var mdlPopup = document.getElementById('mdl-popup');

  if (showPopupBtns.length !== 0) {
    showPopupBtns.forEach(function (btn) {
      return btn.addEventListener('click', showMdlPopup);
    });
  }

  if (mdlPopup) {
    mdlPopup.addEventListener('click', hideMdlPopup);
  }

  function showMdlPopup(e) {
    e.preventDefault();
    var popupId = this.dataset.popupId;
    var popup = document.querySelector("#mdl-popup [data-popup=\"".concat(popupId, "\"]"));

    if (popup) {
      var body = document.querySelector('body');
      popup.classList.add('visible');
      popup.closest('#mdl-popup').classList.add('visible');
      body.classList.add('scroll-lock');
    }
  }

  function hideMdlPopup(e) {
    if (e.target.id === 'mdl-popup' || e.target.classList.contains('mdl-btn-close')) {
      var popup = e.target.closest('#mdl-popup');
      var visiblePopupContent = popup.querySelector('.visible');
      var body = document.querySelector('body');
      visiblePopupContent.classList.remove('visible');
      popup.classList.remove('visible');
      body.classList.remove('scroll-lock');
    }
  }
});
/***************************MODAL HANDLER END*******************************/

