function setSearchListeners() {
  var searchTab = document.querySelector("[data-search=tab]");
  var searchTabInput = document.querySelector("[data-search=input]");
  var searchTabClose = document.querySelector("[data-search=close]");
  var MOBILE_VIEW_CLASS = "mobile-view";

  if (searchTabInput && searchTab) {
    searchTabInput.addEventListener("click", function () {
      var isMobileViewActive = searchTab.classList.contains(MOBILE_VIEW_CLASS);

      if (!isMobileViewActive) {
        searchTab.classList.add(MOBILE_VIEW_CLASS);
      }
    });
  }

  if (searchTabClose && searchTab && searchTabInput) {
    searchTabClose.addEventListener("click", function () {
      searchTab.classList.remove(MOBILE_VIEW_CLASS);
      searchTabInput.value = "";
    });
  }
}

function setMenuListeners() {
  var navbarButtonToggle = document.querySelector("[data-navibar-header-toggle]");
  var navbar = document.querySelector("[data-navibar]");
  var NAVBAR_OPEN_CLASS = "is-open";

  if (navbar && navbarButtonToggle) {
    window.addEventListener("click", function () {
      navbar.classList.remove(NAVBAR_OPEN_CLASS);
      navbarButtonToggle.classList.remove(NAVBAR_OPEN_CLASS);
    });
    navbarButtonToggle.addEventListener("click", function (e) {
      e.stopPropagation();
      navbar.classList.toggle(NAVBAR_OPEN_CLASS);
      navbarButtonToggle.classList.toggle(NAVBAR_OPEN_CLASS);
    });
  }

  if (navbar) {
    navbar.addEventListener("click", function (e) {
      var isOpened = navbar.classList.contains(NAVBAR_OPEN_CLASS);

      if (isOpened) {
        e.stopPropagation();
      }
    });
  }
}

