var navFilterBtns = document.querySelectorAll(".nav-filter-item .nav-filter-item-dropdown");

if (navFilterBtns.length !== 0) {
  navFilterBtns.forEach(function (btn) {
    return btn.addEventListener('click', setActiveFilterBtn);
  });
}

function setActiveFilterBtn(event) {
  navFilterBtns.forEach(function (btn) {
    if (btn !== event.target) {
      btn.classList.remove('active');
      btn.nextElementSibling.classList.remove('active');
    }
  });
  event.target.classList.toggle('active');
  event.target.nextElementSibling.classList.toggle('active');
}

var navFilter = document.querySelector('.nav-filter');

if (navFilter) {
  var hideFilterDropdowns = function hideFilterDropdowns(event) {
    if (!event.target.closest('.nav-filter-item')) {
      var dropDowns = document.querySelectorAll('.nav-filter-dropdown-content');

      if (dropDowns.length !== 0) {
        dropDowns.forEach(function (item) {
          item.classList.remove('active');
          item.previousElementSibling.classList.remove('active');
        });
      }
    }
  };

  navFilter.addEventListener("click", function (event) {
    console.log(event.target.classList);
    var navFilterItemDropdown = document.querySelector("#nav-filter-sortBy .nav-filter-item-dropdown");
    var navFilterDropdownContent = document.querySelector("#nav-filter-sortBy .nav-filter-dropdown-content");
    var mobileNavFilterItemDropdown = document.querySelector("#mobile-nav-filter .nav-filter-item-dropdown");
    var mobileNavFilterDropdownContent = document.querySelector("#mobile-nav-filter .nav-filter-dropdown-content");

    if (!event.target.classList.contains("sortByFilter") && navFilterItemDropdown && navFilterDropdownContent) {
      navFilterItemDropdown.classList.remove("active");
      navFilterDropdownContent.classList.remove("active");
    }

    if (!event.target.classList.contains("navFilter") && mobileNavFilterItemDropdown && mobileNavFilterDropdownContent) {
      mobileNavFilterItemDropdown.classList.remove("active");
      mobileNavFilterDropdownContent.classList.remove("active");
    }
  }); // -------- Hide filters dropdown when click on page ----------------

  var body = document.querySelector('body');
  body.addEventListener('click', hideFilterDropdowns);
}
